@import '../global.scss';

.menu {
    width: 300px;
    height: 100vh;
    background-color: $mainColor;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    &.active {
        right: 0;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 35px;

        li {
            margin-bottom: 25px;
            font-weight: lighter;

            a {
                text-decoration: none;
                color: white;

                &:hover {
                    color: lightgray;
                }
            }
        }
    }   
}