@import '../global.scss';

.navBar {
    color: $mainColor;
    background-color: white;
    width: 100%;
    height: 70px;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 2s ease;

    @media screen and (max-width: 664px) {
        height: 90px;
    }

    .nav-wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            align-items: center;
            
            @media screen and (max-width: 768px) {
                flex-wrap: wrap;
            }

            .logo {
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
            }

            .itemContainer {
                display: flex;
                align-items: center;

                .icon {
                    padding-left: 30px;
                    padding-right: 5px;
                }

                span {
                    font-weight: 500;
                }
            }
        }

        .right {

            .hamburger {
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                padding-left: 10px;
                
                span {
                    width: 100%;
                    height: 3px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    &.active {
        background-color: $mainColor;
        color: white;

        .hamburger {
            span {
                &:first-child {
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:last-child {
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}